import * as ko from "knockout";
import { GalleryViewModel } from "./GalleryViewModel";

function getParameterByName(name: string, url?: string) {
  if (!url) url = window.location.href;
  name = name.replace(/[\[\]]/g, "\\$&");
  var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
    results = regex.exec(url);
  if (!results) return "";
  if (!results[2]) return "";
  return decodeURIComponent(results[2].replace(/\+/g, " "));
}
console.log("running gallery init");
var id = parseInt(getParameterByName("id"));
var ViewModel = new GalleryViewModel();
ko.applyBindings(ViewModel);

console.log(ViewModel.galleries);
var gallery = ViewModel.galleries().filter(function (g) {
  return g.id == id;
})[0];

if (gallery) {
  ViewModel.displayedGalleries.push(gallery);
  if (id >= ViewModel.maxId) {
    (
      document.getElementsByClassName("gallery-nav-next")[0] as HTMLDivElement
    ).style.visibility = "hidden";
    (
      document.getElementsByClassName("gallery-nav-next")[1] as HTMLDivElement
    ).style.visibility = "hidden";
  }
  if (id == 0) {
    (
      document.getElementsByClassName("gallery-nav-prev")[0] as HTMLDivElement
    ).style.visibility = "hidden";
    (
      document.getElementsByClassName("gallery-nav-prev")[1] as HTMLDivElement
    ).style.visibility = "hidden";
  }
}
